<template>
  <div>
    <section class="chatlist">
      <mt-loadmore
        top-pull-text="加载更多"
        top-drop-text="释放加载"
        @top-status-change="handleTopChange"
        ref="loadmore"
      >
        <ul>
          <template v-for="item in records">
            <li class="chat-mine" v-if="item.type == 1">
              <div class="chat-user"><img src="../assets/user.png" /></div>
              <div class="time">
                <cite
                  ><i>{{ item.time }}</i
                  >{{ item.name }}</cite
                >
              </div>
              <div class="chat-text" v-html="item.content"></div>
            </li>
            <li v-if="item.type == 2">
              <div class="chat-user"><img src="../assets/default.png" /></div>
              <div class="time">
                <cite
                  >{{ item.name }}<i>{{ item.time }}</i></cite
                >
              </div>
              <div class="chat-text" v-html="item.content"></div>
            </li>
          </template>
        </ul>
      </mt-loadmore>
    </section>
    <section class="foot">
      <mt-field
        id="txtContent"
        placeholder="请输入消息"
        class="con"
        v-model="content"
      ></mt-field>
      <span class="btn btn-send" v-on:click="sendMsg">发送</span>
    </section>
  </div>
</template>

<script>
import util from "../common/util";
import { Toast } from "mint-ui";

export default {
  name: "chatlist",
  data() {
    return {
      content: "",
      topStatus: "",
      baseUrl:
        process.env.NODE_ENV === "development" ? "http://localhost:5089" : "",
      //聊天记录
      records: [],
      // 表情
      EXPS: [],
      UserId: "",
    };
  },
  methods: {
    getEXP: function (pageNow, pageSize) {
      return this.EXPS.slice((pageNow - 1) * pageSize, pageSize * pageNow);
    },

    loadMessage() {
      var t = this;
      setInterval(function () {
        if (t.UserId) {
          t.$http
            .get(t.baseUrl + "/api/Message/" + t.UserId)
            .then((res) => {
              console.log(res.data);
              if (res.data) {
                for (var i = 0; i < res.data.length; i++) {
                  var sss = res.data[i]
                    .replace(/\r/g, "<br>")
                    .replace(/\n/g, "<br>");
                  console.log(sss);
                  t.records.push({
                    type: 2,
                    time: util.formatDate.format(
                      new Date(),
                      "yyyy-MM-dd hh:mm:ss"
                    ),
                    name: "",
                    content: sss,
                  });
                }
                t.saveRecords();
              }
            })
            .catch((err) => {
              console.log(err);
            });
        }
      }, 1000);
    },
    //发送消息
    sendMsg: function () {
      if (this.content == "") {
        Toast("请输入消息");
        return;
      }
      this.$http
        .get(
          this.baseUrl +
            "/api/Message?Message=" +
            this.content +
            "&userId=" +
            this.UserId
        )
        .then((res) => {
          console.log(res);
        })
        .catch((err) => {
          console.log(err);
        });

      this.records.push({
        type: 1,
        time: util.formatDate.format(new Date(), "yyyy-MM-dd hh:mm:ss"),
        name: "",
        content: this.content,
      });

      this.content = "";
      this.scrollToBottom();
      this.saveRecords();
    },
    saveRecords() {
      localStorage.setItem("records", JSON.stringify(this.records));
    },
    //聚焦输入框
    focusTxtContent: function () {
      document.querySelector("#txtContent input").focus();
    },
    //滚动条滚动到底部
    scrollToBottom: function () {
      setTimeout(function () {
        var chatlist = document.getElementsByClassName("chatlist")[0];
        chatlist.scrollTop = chatlist.scrollHeight;
      }, 100);
    },
    handleTopChange(status) {
      this.topStatus = status;
    },
    uuid(len, radix) {
      var chars =
        "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz".split(
          ""
        );
      var uuid = [],
        i;
      radix = radix || chars.length;
      for (i = 0; i < len; i++) uuid[i] = chars[0 | (Math.random() * radix)];
      return uuid.join("");
    },
  },
  mounted: function () {
    this.scrollToBottom();
    this.focusTxtContent();
    this.UserId = localStorage.getItem("UserId");
    if (!this.UserId) {
      this.UserId = this.uuid(16);
      localStorage.setItem("UserId", this.UserId);
    }
    this.loadMessage();
    this.records = JSON.parse(localStorage.getItem("records")) || [];
  },
};
</script>

<style>
.chatlist {
  position: absolute;
  top: 60px;
  bottom: 48px;
  left: 0px;
  right: 0px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding: 10px;
}

.chatlist-bottom {
  bottom: 48px;
}

.chatlist-bottom-collapse {
  bottom: 198px;
}

.chatlist ul {
  min-height: 300px;
}

.chatlist ul .chat-mine {
  text-align: right;
  padding-left: 0;
  padding-right: 60px;
}

.chatlist ul li {
  position: relative;
  /*font-size: 0;*/
  margin-bottom: 10px;
  padding-left: 60px;
  min-height: 68px;
}

.chat-mine .chat-user {
  left: auto;
  right: 3px;
}

.chat-user {
  position: absolute;
  left: 3px;
}

.chat-text,
.chat-user {
  display: inline-block;
  vertical-align: top;
  /*font-size: 14px;*/
}

.chat-user img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
}

.time {
  width: 100%;
}

cite {
  left: auto;
  right: 60px;
  text-align: right;
}

cite {
  font-style: normal;
  line-height: 24px;
  font-size: 12px;
  white-space: nowrap;
  color: #999;
  text-align: left;
}

cite i {
  font-style: normal;
  padding-left: 5px;
  padding-right: 5px;
  font-size: 12px;
}

.chat-mine .chat-text {
  margin-left: 0;
  text-align: left;
  background-color: #33df83;
  color: #fff;
}

.chat-text {
  position: relative;
  line-height: 22px;
  /*margin-top: 25px;*/
  padding: 10px 15px;
  background-color: #eee;
  border-radius: 3px;
  color: #333;
  word-break: break-all;
  max-width: 462px\9;
}

.chat-text,
.chat-user {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
}

.chat-text img {
  max-width: 100%;
  vertical-align: middle;
}

.chat-user {
  position: absolute;
  left: 3px;
}

.chat-text:after {
  content: "";
  position: absolute;
  left: -10px;
  top: 15px;
  width: 0;
  height: 0;
  border-style: solid dashed dashed;
  border-color: #eee transparent transparent;
  overflow: hidden;
  border-width: 10px;
}

.chat-mine .chat-text:after {
  left: auto;
  right: -10px;
  border-top-color: #33df83;
}

.foot {
  width: 100%;
  min-height: 48px;
  position: fixed;
  bottom: 0px;
  left: 0px;
  background-color: #f8f8f8;
}

.foot .con {
  position: absolute;
  left: 1px;
  right: 44px;
}

.foot .btn-plus {
  width: 28px;
  padding: 9px 3px;
  position: absolute;
  left: 0px;
  border-left: 1px solid #d9d9d9;
}

.foot .btn-plus i {
  font-size: 2em;
  color: #ccc;
}

.foot .btn-face {
  width: 28px;
  padding: 9px 3px 9px 0px;
  position: absolute;
  left: 35px;
  /*border-right: 1px solid #d9d9d9;*/
}

.foot .btn-face i {
  font-size: 2em;
  color: #d9d9d9;
}

.foot .selbox {
  height: 150px;
  margin-top: 48px;
  border-top: 1px solid #d9d9d9;
}

.show {
  display: block;
}

.hide {
  display: none;
}

.face-box {
  /* position: absolute; */
  /* top: 48px; */
  /* left: 0px; */
  /* right: 0px; */
  /* bottom: 0px; */
  padding: 15px 15px 0px 15px;
  overflow: hidden;
  width: 290px;
  margin: 0px auto;
  height: 135px;
}

.face-box li {
  width: 30px;
  float: left;
  padding: 6px 10px 0px 8px;
}

.btn {
  display: inline-block;
  vertical-align: top;
  font-size: 14px;
  line-height: 32px;
  margin-left: 5px;
  padding: 0 6px;
  background-color: #33df83;
  color: #fff;
  border-radius: 3px;
}

.btn-send {
  position: absolute;
  right: 0px;
  top: 8px;
}
</style>


